
import * as Styled from './styles';
import { Heading } from '../Heading';
import {LogoLink} from '../LogoLink';


export type HeaderProps = {
  blogName:string;
  description:string;
  logo:string;
  showText?:boolean;
};

export const Header = ({
  blogName,
  description,
  logo,
  showText = false,

}: HeaderProps) => {

    return (
        <Styled.Wrapper>
          <LogoLink link="/" srcImg={logo} text={blogName} />
          {showText && (
            <Styled.Content>
              <Heading size="small" as="h2" colorDark={true}>
                {blogName}
              </Heading>
              <p>{description}</p>
            </Styled.Content>
          )}
        </Styled.Wrapper>
    );
  };
